"use client";

import React, { memo } from "react";
import Link from "next/link";
import Image from "next/image";
import { motion } from "framer-motion";
import { MessageCircle, FileText, Bot, ArrowRight } from "lucide-react";
import { services } from "@/lib/constants";
import { fadeInUp, staggerContainer } from "@/lib/animations";
import Card from "@/components/ui/Card";
import Section from "@/components/layout/Section";

const iconMap = {
  "message-circle": MessageCircle,
  "file-text": FileText,
  bot: Bot,
};

const serviceImageMap: Record<number, string> = {
  1: "/images/services/service-chatbot.jpg",
  2: "/images/services/service-document.jpg",
  3: "/images/services/service-agents.jpg",
};

const Services: React.FC = memo(() => {
  return (
    <Section id="services">
      <motion.div
        variants={staggerContainer}
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true, margin: "-100px" }}
      >
        <motion.div
          className="text-center mb-12"
          variants={fadeInUp}
        >
          <h2 className="text-4xl md:text-5xl lg:text-6xl font-bold text-primary-navy mb-4 tracking-tight">
            Our Services
          </h2>
          <p className="text-lg text-neutral-text-secondary max-w-2xl mx-auto">
            Cutting-edge AI solutions tailored to your business needs
          </p>
        </motion.div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {services.map((service, index) => {
            const Icon = iconMap[service.icon as keyof typeof iconMap] || Bot;
            const imagePath = serviceImageMap[service.id];

            return (
              <motion.div key={service.id} variants={fadeInUp}>
                <Card className="h-full flex flex-col group cursor-pointer overflow-hidden">
                  {/* Service Image */}
                  {imagePath && (
                    <div className="relative w-full h-48 mb-4 -mx-6 -mt-6 overflow-hidden">
                      <Image
                        src={imagePath}
                        alt={service.title}
                        fill
                        className="object-cover group-hover:scale-105 transition-transform duration-500"
                        onError={(e) => {
                          const target = e.target as HTMLImageElement;
                          target.style.display = "none";
                        }}
                      />
                      <div className="absolute inset-0 bg-primary-navy/10" />
                      <div className="absolute top-4 left-4 w-12 h-12 rounded-lg bg-primary-navy p-3 text-white shadow-lg">
                        <Icon className="h-6 w-6" />
                      </div>
                    </div>
                  )}
                  
                  {/* Fallback if no image */}
                  {!imagePath && (
                    <div className="mb-4">
                      <div className="w-12 h-12 rounded-lg bg-primary-navy p-3 text-white mb-4 group-hover:scale-110 transition-transform duration-300">
                        <Icon className="h-6 w-6" />
                      </div>
                    </div>
                  )}

                  <div className="flex-1">
                    <h3 className="text-2xl font-bold text-primary-navy mb-2">
                      {service.title}
                    </h3>
                    <p className="text-neutral-text-secondary mb-4">{service.description}</p>
                  </div>

                  <div className="mt-auto pt-4 border-t border-neutral-border">
                    <Link
                      href="/services"
                      className="inline-flex items-center text-primary-navy font-medium hover:text-primary-blue transition-colors group-hover:gap-2 gap-1"
                    >
                      Learn More
                      <ArrowRight className="h-4 w-4 transition-transform group-hover:translate-x-1" />
                    </Link>
                  </div>
                </Card>
              </motion.div>
            );
          })}
        </div>
      </motion.div>
    </Section>
  );
});

Services.displayName = "Services";

export default Services;
